<template>
  <div class="container mt-5">
    <div class="row justify-content-center">
      <div class="col-md-6">
        <div class="card">
          <div class="card-header">
            QR Code Generator
          </div>
          <div class="card-body">
            <form>
              <div class="form-group">
                <label for="qrContent">QR Code Content:</label>
                <input
                  type="text"
                  class="form-control"
                  v-model="qrCodeContent"
                  placeholder="Enter URL for QR code"
                />
              </div>
              <button type="button" class="btn btn-primary mt-2" @click="generateQRCode">
                Generate QR Code
              </button>
            </form>
            <button v-if="qrCode" type="button" class="btn btn-success mt-2" @click="downloadQRCode">
                Download QR Code
              </button>
            <div class="mt-3" id="qrcode">
              <img :src="qrCode" alt="QR Code" v-if="qrCode" style="height: 200px; width: 200px;" />
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import QRCodeGenerator from 'qrcode-generator';
import { ref } from 'vue';

const qrCodeContent = ref('');
const qrCode = ref(null);

function generateQRCode() {
  try {
    // Create a QR code
    const qr = QRCodeGenerator(0, 'H');
    qr.addData(qrCodeContent.value);
    qr.make();
    qrCode.value = qr.createDataURL(10, 0);
  } catch (error) {
    console.error('Error generating QR code:', error.message);
  }
}

function downloadQRCode() {
  if (qrCode.value) {
    const link = document.createElement('a');
    link.href = qrCode.value;
    link.download = 'qrcode.png';
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  }
}
</script>

